"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

// Definição simplificada de permissões para teste
const permissoesTeste = {
  ESTOQUE_VISUALIZAR: true,
  ESTOQUE_ADICIONAR: false,
  VENDAS_VISUALIZAR: true,
  VENDAS_CRIAR: false,
  FILA_VISUALIZAR: true,
  USUARIOS_VISUALIZAR: false,
}

export default function PermissoesSimplificadoPage() {
  const [permissoes, setPermissoes] = useState(permissoesTeste)
  const [logs, setLogs] = useState<string[]>([])

  // Função para adicionar logs
  const addLog = (message: string) => {
    setLogs((prev) => [...prev, `${new Date().toISOString().split("T")[1].split(".")[0]}: ${message}`])
  }

  // Efeito para registrar quando o componente é montado
  useEffect(() => {
    addLog("Componente montado com sucesso")
    addLog(`Estado inicial das permissões: ${JSON.stringify(permissoes)}`)
  }, [])

  // Função para alternar permissão
  const togglePermissao = (permissaoId: string) => {
    addLog(`Alternando permissão: ${permissaoId}`)
    setPermissoes((prev) => ({
      ...prev,
      [permissaoId]: !prev[permissaoId as keyof typeof prev],
    }))
  }

  return (
    <div className="container mx-auto py-6">
      <h1 className="text-2xl font-bold mb-6">Permissões Simplificadas (Teste)</h1>

      <Card className="mb-6">
        <CardHeader>
          <CardTitle>Permissões de Teste</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {Object.entries(permissoes).map(([permissaoId, valor]) => (
              <div key={permissaoId} className="flex items-center space-x-2">
                <input
                  type="checkbox"
                  id={permissaoId}
                  checked={valor}
                  onChange={() => togglePermissao(permissaoId)}
                  className="h-4 w-4 rounded border-gray-300 text-blue-600 focus:ring-blue-500"
                />
                <label htmlFor={permissaoId} className="text-sm font-medium cursor-pointer">
                  {permissaoId}
                </label>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      <Card className="mb-6">
        <CardHeader>
          <CardTitle>Logs</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="bg-gray-100 p-4 rounded-md">
            <pre className="whitespace-pre-wrap text-sm">
              {logs.map((log, index) => (
                <div key={index}>{log}</div>
              ))}
            </pre>
          </div>
        </CardContent>
      </Card>

      <div className="flex justify-end">
        <Button
          onClick={() => {
            addLog("Botão de teste clicado")
            alert("Estado atual das permissões: " + JSON.stringify(permissoes, null, 2))
          }}
        >
          Testar Estado
        </Button>
      </div>
    </div>
  )
}
